/* ===== Global ===== */
body {
  margin: 0;
  background-color: #f7f9fc;
}

.App {
  max-width: 1200px;
  margin: auto;
  padding: 20px;
  font-family: Arial, Helvetica, sans-serif;
}

/* ===== Title ===== */
h1 {
  text-align: center;
  color: #2b6cb0;
  margin-bottom: 30px;
}

/* ===== Search / Filters ===== */
.search {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
  margin-bottom: 30px;
}

.search select,
.search input {
  padding: 10px;
  border-radius: 6px;
  border: 1px solid #ccc;
  min-width: 160px;
}

/* ===== Property Grid ===== */
.property-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 20px;
}

/* ===== Property Card ===== */
.property-card {
  background-color: #ffffff;
  border-radius: 10px;
  padding: 18px;
  border: 1px solid #e2e8f0;
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.property-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 6px 18px rgba(0, 0, 0, 0.08);
}

.property-card h3 {
  margin-top: 0;
  color: #2b6cb0;
}

/* ===== Property Details ===== */
.property-details {
  max-width: 600px;
  margin: auto;
  background-color: #ffffff;
  padding: 25px;
  border-radius: 10px;
  border: 1px solid #e2e8f0;
}

.property-details button {
  margin-top: 15px;
}

/* ===== Buttons ===== */
button {
  padding: 10px 14px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  background-color: #2b6cb0;
  color: white;
  font-size: 14px;
}

button:hover {
  background-color: #234e85;
}

button:disabled {
  background-color: #a0aec0;
  cursor: not-allowed;
}

/* ===== Favourites ===== */
.favourites {
  margin-bottom: 40px;
}

.favourites h2 {
  color: #2b6cb0;
  margin-bottom: 15px;
}
